<template>
  <div id="toPayList" >
    <!---->
    <div class="public_stylecss">
      <div class="clear wantMoney">
        <el-breadcrumb-item :to="{ path: '/index' }">
          <img class="back__" src="../../assets/images/back.png" alt="" style="width: 1rem;">
        </el-breadcrumb-item>
        <p class="fr union_head" @click="boxWarp">
          <img src="../../assets/images/switchover.png" alt="">
          <span  class="unionName"></span>
        </p>
      </div>
      <div class="union_second payed passive">
        <p>{{payMoney.toFixed(2)}}</p>
        <span>()</span>
      </div>
      <!-- passive -->
      <div class="union_second  unpay clear">
        <div class="fl">
          <p>{{totalMoney.toFixed(2)}}</p>
          <span>()</span>
        </div>
        <div class="fr payment" @click="showModel3_">
          
        </div>
        <div class="box-wrap3" style="display: none">
          <div class="mask" @click="hide3_"></div>
          <div class="box">
            <p>{{totalMoney.toFixed(2)}}?</p>
            <div class="fr" @click="send3_()"></div>
            <div style="border-left:none" @click="hide3_"></div>
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div class=" detail-wrap clear">
      <div class='payTab flex flex-pack-center'>
        <div class="focusPay"></div>
        <div class=""></div>
      </div>
      <p class="record"><p>
      <!---->
      <div class="unpay ">
        <ul >
          <li class="clear" v-for="(item1, index) in unPayList">
            <div class="clear fl">
              <div class="fl clear">
                <div class='fl'>
                  <p class="">{{item1.unionName}}</p>
                  <span style="color: #7e7e7e">{{item1.time}}</span>
                </div>
                <div class='fl'>
                  <el-button class='goPay'  @click="showModel_(index)"></el-button>
                </div>
                <div class="box-wrap2" style="display: none">
                  <div class="mask" @click="hide1_"></div>
                  <div class="box">
                    <p>{{item1.money.toFixed(2)}}?</p>
                    <div class="fr" @click="send1_(item1.id)"></div>
                    <div style="border-left:none" @click="hide1_"></div>
                  </div>
                </div>
              </div>
            </div>
            <div class="clear fr">
              <div class="fr">
                <span>{{item1.name}}</span>
                <p class="" style="color: #90e88f;text-align: right;">-{{item1.money.toFixed(2)}}</p>
              </div>
            </div>
          </li>
        </ul>
        <div class="loadMore hasPayLoadMore1"  @click="loadMore1"></div>
        <div class="nothing noPayNothing" style="display: none"></div>
      </div>
      <!---->
      <div class="payed passive">
        <ul >
          <li class="clear" v-for="(item, index) in payList">
            <div class="clear fl">
              <div class='fl'>
                <p>
                  {{item.unionName}}
                </p>
                <span style="color: #7e7e7e">
                      {{item.time}}
                    </span>
              </div>
            </div>
            <div class="clearfr">
              <div class="fr">
                <span>{{item.name}}</span>
                <p class="" style="color: #90e88f;text-align: right;">{{item.money.toFixed(2)}}</p>
              </div>
            </div>
          </li>
        </ul>
        <div class="loadMore hasPayLoadMore2" style="display: none" @click="loadMore2"></div>
        <div class="nothing hasPayNothing" style="display: none"></div>
      </div>
    </div>
    <!---->
    <div class="box-wrap" style="display: none">
      <div class="mask" @click="hide_"></div>
      <div class="box">
        <div id="triangle-up"></div>
        <ul>
          <li  @click="fullyLoaded"></li>
          <li v-for="(item, index) in unionList" @click="partLoaded(item.id,item.name)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>

</template>

<script>
  import $http from '@/utils/http.js'
  import $todate from '@/utils/todate.js'
  export default {
    name: 'toPayList',
    data() {
      return {
        //
        unPayList:[],
        //
        payList:[],
        //
        unionList:[],
        //
        totalMoney:0,
        //
        payMoney:0,
        //
        size:6,
        //
        current:1,
        //id
        unionId:'',
        //
        totalMoney12:0
      }
    },
    mounted(){
      //
      $('.payTab div').on('touchend', function(){
        let index = $(this).index();
        $(this).addClass('focusPay').siblings('.payTab div').removeClass('focusPay');
        if(index == 1){//
          if($(".payed").hasClass("passive")){
            $(".payed").removeClass("passive");
            $(".unpay").addClass("passive");
          }
        }else if(index == 0){//
          if($(".unpay").hasClass("passive")){
            $(".payed").addClass("passive");
            $(".unpay").removeClass("passive");
          }
        }
      });
    },
    methods: {
      //
      boxWarp(){
          $('.box-wrap').show();
      },
      //
      hide_(){
        $('.box-wrap').hide();
      },
      //
      showModel_(index){
        $($('.box-wrap2')[index]).show();
      },
      hide1_(){
        $('.box-wrap2').hide();
      },
      //id
      send1_(id){
        $('.box-wrap2').hide();
//        
//        console.log(window.location.href.lastIndexOf('/'));
        let did=id;
        let url='toPayList';
        $http.post(`/unionH5Brokerage/onePay/${did}?url=${url}`)
          .then(res => {
            if(res.data.data) {
              //
              location.href = res.data.data;
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
      },
      //
      showModel3_(){
        if(this.totalMoney>0) {
          $('.box-wrap3').show();
        }else{
          this.$message({ showClose: true, message: '', type: 'error', duration: 3000 });
        }
      },
      //
      hide3_(){
        $('.box-wrap3').hide(200);
      },
      //
      send3_(){
        let uid=this.unionId;
        //
        $http.get(`/unionH5Brokerage/unPaySum?unionId=${uid}`)
          .then(res => {
            if(res.data.data) {
              this.totalMoney12 = res.data.data;
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
        let url='toPayList';
        $http.post(`/unionH5Brokerage/allPay?unionId=${uid}&fee=${this.totalMoney12}&url=${url}`)
          .then(res => {
            if(res.data.data) {
              //
              location.href = res.data.data;
              //
//            history.go(0)
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
        $('.box-wrap3').hide(200);
      },
//    ''
      fullyLoaded(){
        //
        this.current=1;
        //Id
        this.unionId='';
        document.querySelector('.unionName').innerHTML='';
        //-------------1
        $http.get(`/unionH5Brokerage/unPay/list?size=${this.size}&current=${this.current}`)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
                this.totalMoney = 0;
                this.unPayList = [];
              } else if (res.data.data.records.length < this.size) {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').hide();
              }
              else {
                console.log(res.data.data.records)
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              this.unPayList = res.data.data.records;
              this.unPayList.forEach((v, i) => {
                v.time = $todate.todate(new Date(v.time));
              });
              //
              $http.get(`/unionH5Brokerage/unPaySum`)
                .then(res => {
                  if(res.data.data){
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 0});
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
        //-------------2
          $http.get(`/unionH5Brokerage/pay/list?size=${this.size}&current=${this.current}`)
            .then(res => {
              if(res.data.data) {
                console.log(res.data.data.records)
                if (res.data.data.records.length === 0) {
                  this.payMoney = 0;
                  this.payList = [];
                  $('.hasPayNothing').show();
                  $('.hasPayLoadMore2').hide();
                } else if (res.data.data.records.length < this.size) {
                  $('.hasPayNothing').hide();
                  $('.hasPayLoadMore2').hide();
                }
                else {
                  $('.hasPayNothing').hide();
                  $('.hasPayLoadMore2').show();
                }
                //
                this.payList = res.data.data.records;
                this.payList.forEach((v, i) => {
                  v.time = $todate.todate(new Date(v.time));
                })
                //
                $http.get(`/unionH5Brokerage/paySum`)
                  .then(res => {
                    if(res.data.data) {
                      this.payMoney = res.data.data;
                    }
                  })
                  .catch(err => {
                    this.$message({showClose: true, message: err.toString(), type: 'error', duration: 0});
                  });
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
            });
        $('.box-wrap').hide(300);
      },
//      '';
      partLoaded(did,uname){
        let Uid=did;
        //id
        this.unionId=did;
        document.querySelector('.unionName').innerHTML=uname;
        console.log(Uid);
        //-----------------1
        $http.get(`/unionH5Brokerage/unPay/list?size=${this.size}&current=${this.current}&unionId=${Uid}`)
          .then(res => {
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
                this.totalMoney = 0;
                this.unPayList = [];
              } else if (res.data.data.records.length < this.size) {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').hide();
              }
              else {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              this.unPayList = res.data.data.records;
              this.unPayList.forEach((v, i) => {
                v.time = $todate.todate(new Date(v.time));
              })
              //
              $http.get(`/unionH5Brokerage/unPaySum?unionId=${Uid}`)
                .then(res => {
                  if(res.data.data) {
                    this.totalMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 0});
                });
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
        //----------------2
        $http.get(`/unionH5Brokerage/pay/list?size=${this.size}&current=${this.current}&unionId=${Uid}`)
          .then(res=>{
            if(res.data.data) {
              if (res.data.data.records.length === 0) {
                this.payMoney = 0;
                this.payList = [];
                $('.hasPayNothing').show();
                $('.hasPayLoadMore2').hide();
              } else if (res.data.data.records.length < this.size) {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').hide();
              }
              else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').show();
              }
              //
              this.payList = res.data.data.records;
              this.payList.forEach((v, i) => {
                v.time = $todate.todate(new Date(v.time));
              })
              $http.get(`/unionH5Brokerage/paySum?unionId=${Uid}`)
                .then(res => {
                  if(res.data.data) {
                    this.payMoney = res.data.data;
                  }
                })
                .catch(err => {
                  this.$message({showClose: true, message: err.toString(), type: 'error', duration: 0});
                });
            }
          }
        )
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
        });
        //
        $('.box-wrap').hide(300);
      },
      //
      loadMore1(){
        let uid=this.unionId;
        console.log(++this.current);
        $http.get(`/unionH5Brokerage/unPay/list?size=${this.size}&current=${this.current}&unionId=${uid}`)
          .then(res => {
            if(res.data.data) {
              let list = res.data.data.records;
              if (list.length < this.size) {
                $('.noPayNothing').show();
                $('.hasPayLoadMore1').hide();
              } else {
                $('.noPayNothing').hide();
                $('.hasPayLoadMore1').show();
              }
              //
              list.forEach((v, i) => {
                v.time = $todate.todate(new Date(v.time));
              })
              this.unPayList = this.unPayList.concat(list);
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
      },
      //
      loadMore2(){
        let uid=this.unionId;
        console.log(++this.current);
        $http.get(`/unionH5Brokerage/pay/list?size=${this.size}&current=${this.current}&unionId=${uid}`)
          .then(res => {
            if(res.data.data) {
              let list = res.data.data.records;
              if (list.length < this.size) {
                $('.hasPayNothing').show();
                $('.hasPayLoadMore2').hide();
              } else {
                $('.hasPayNothing').hide();
                $('.hasPayLoadMore2').show();
              }
              //
              list.forEach((v, i) => {
                v.time = $todate.todate(new Date(v.time));
              })
              this.payList = this.payList.concat(list);
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          });
        console.log(this.unCommission);
      },
    },
    created (){
      //title
      $("#title_").text('')
//--------------------------------------------------------------------------1
//      
      $http.get(`/unionH5Brokerage/unPay/list?size=${this.size}&current=${this.current}`)
        .then(res => {
          if(res.data.data) {
            if (res.data.data.records.length === 0) {
              $('.noPayNothing').show();
              $('.hasPayLoadMore1').hide();
              this.totalMoney = 0;
              this.unPayList = [];
            } else if (res.data.data.records.length < this.size) {
              $('.noPayNothing').hide();
              $('.hasPayLoadMore1').hide();
            }
            else {
              $('.noPayNothing').hide();
              $('.hasPayLoadMore1').show();
            }
            //
            this.unPayList = res.data.data.records;
            this.unPayList.forEach((v, i) => {
              v.time = $todate.todate(new Date(v.time));
            })
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
        });
      //
      $http.get(`/unionH5Brokerage/unPaySum`)
        .then(res => {
          if(res.data.data) {
            this.totalMoney = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
        });
// ------------------------------------------------------------------------------2
      //
      $http.get(`/unionH5Brokerage/pay/list?size=${this.size}&current=${this.current}`)
        .then(res => {
          if(res.data.data) {
            if (res.data.data.records.length === 0) {
              this.payMoney = 0;
              this.payList = [];
              $('.hasPayNothing').show();
              $('.hasPayLoadMore2').hide();
            } else if (res.data.data.records.length < this.size) {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore2').hide();
            }
            else {
              $('.hasPayNothing').hide();
              $('.hasPayLoadMore2').show();
            }
            //
            this.payList = res.data.data.records;
            this.payList.forEach((v, i) => {
              v.time = $todate.todate(new Date(v.time));
            })
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
          console.log(666)
        });
      //
      $http.get(`/unionH5Brokerage/paySum`)
        .then(res => {
          if(res.data.data) {
            this.payMoney = res.data.data;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
        });
//-----------------------------------------------------------------------3
      $http.get(`/unionH5Brokerage/unionList`)
        .then(res => {
          let that_=this;
          if(res.data.data){
            res.data.data.forEach((item,index)=>{
              that_.unionList.push(item);
            });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 0 });
        });
    }
  }
</script>
<style lang='less' rel="stylesheet/less" scoped>
.hasPayLoadMore1,.hasPayLoadMore2{
  font-size: 0.85rem;
  color: #39ACFF;
  text-align: center;
  padding: 0.3rem;
}
.nothing{
  margin: 0.8rem 6rem;
  font-size: 0.85rem;
}
</style>



